<template>
  <aside
    @click.stop="">
    <div class="avator">
      <img src="static/images/thum.jpg">
    </div>
    <div class="set-item"
         @click="send()">
      高级设置
    </div>
    <div class="set-item">高级设置</div>
    <div class="set-item">高级设置</div>
    <div class="set-item">高级设置</div>
    <div class="set-item">高级设置</div>
  </aside>
</template>

<script>
  export default {
    name: "user-setting",
    methods:{
      send(){
        // this.$store.commit('setData', [0,0,0,0,0]);
        this.$store.dispatch('setData', [0,0,0,0,0]);
        console.log(this.$store.state.data);
        this.$emit('msgFromSon','msg from son');
      }
    }
  }
</script>

<style scoped>
  aside {
    height: 100%;
    width: 70%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 102;
    padding-top: 1.5rem;
    background-color: #fff;
    border-right: 1px solid #e52f2f;
    box-shadow: 0 0 10px 8px #e52f2f;
  }

  aside > div {
    border-bottom: 1px solid #EEEEEE;
    line-height: 2.4rem;
    font-size: 14px;
  }

  .avator {
    width: 100%;
    height: 3rem;
    padding-bottom: .5rem;
    text-align: center;
  }

  .avator > img {
    height: 3rem;
    width: 3rem;
    border-radius: 50%;
  }

  .set-item {
    padding-left: 1rem;
  }
</style>



